import React, { Component } from 'react';
import request from 'superagent'
import { Link, withRouter } from "react-router-dom";
import { alert } from '../components/Alert';

class Dashboard extends Component {
  state = {
    currentLocationWeather: null,
    value: '',
    city: []
  };

  componentDidMount() {
    if (sessionStorage.getItem('user') === sessionStorage.getItem('typedUsername') && sessionStorage.getItem('pass') === sessionStorage.getItem('typedPassword')) {
      navigator.geolocation.getCurrentPosition(this.getCurrentLocationWeather);
    } else {
      this.props.history.push('/login');
    }
  }

  getCurrentLocationWeather = coordinates => {
    const lat = coordinates.coords.latitude;
    const lon = coordinates.coords.longitude;

    request
      .get('https://api.weatherbit.io/v2.0/current')
      .query({ lat })
      .query({ lon })
      .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
      .then(res => {
        if (res.body) {
          this.setState({
            currentLocationWeather: res.body.data[0]
          });
        } else {
          alert('No results for your location', 'error')
        }
      })
      .catch(err => {
        alert(err.message, 'error');
      });
  }

  handleChange = (event) => {
    this.setState({ value: event.target.value });
  }

  handleSubmit = (event) => {
    if (this.state.value) {
      request
        .get('https://api.weatherbit.io/v2.0/current')
        .query({ 'city': this.state.value })
        .query({ 'key': 'd5c3391c63af47658c84946c5fe654e5' })
        .then(res => {
          if (res.body) {
            this.setState(prevState => ({
              city: [...prevState.city, res.body.data[0]]
            }));
          } else {
            alert('No results for choosen city', 'error')
          }
        })
        .catch(err => {
          alert(err.message, 'error');
        });
    } else {
      alert('Please, enter some value', 'error');
    }

    event.preventDefault();
  }

  render() {

    const weather = this.state.city.map((city, index) => {
      return (
        <div key={index} className="col-12 col-md-3 my-3">
        <div className="text-center border">
          <h5>{city.city_name}</h5>
          <h6>{city.weather.description}</h6>
          <h6>{city.temp}&deg;</h6>
          <img height="50" width="50" alt={city.weather.icon} src={`https://www.weatherbit.io/static/img/icons/${city.weather.icon}.png`}></img>
        </div>
        </div>
      )
    })

    return (
      <React.Fragment>
        <header className="App-header">
          <div className="row py-4">
            <div className="col-12 d-flex justify-content-between">
              <form onSubmit={this.handleSubmit}>
                <input type="text" value={this.state.value} onChange={this.handleChange} placeholder="Enter city" className="d-block mb-3 form-control" />
                <input type="submit" value="Submit" className="btn btn-dark" />
              </form>
              <Link to="/login">
                <button className="btn btn-danger">Log out</button>
              </Link>
            </div>
          </div>
        </header>
        <main className="py-3">
          <div className="row">
          {
            this.state.currentLocationWeather
              ?
              <div className="col text-center border-bottom">
                <h4>Weather on your location</h4>
                <h5>{this.state.currentLocationWeather.city_name}</h5>
                <h6>{this.state.currentLocationWeather.weather.description}</h6>
                <h6>{this.state.currentLocationWeather.temp}&deg;</h6>
                <img height="50" width="50" alt={this.state.currentLocationWeather.weather.icon} src={`https://www.weatherbit.io/static/img/icons/${this.state.currentLocationWeather.weather.icon}.png`}></img>
              </div>
              :
              null
          }
          </div>
          {
            this.state.city.length
              ?
              <div>
                <h5 className="text-center mt-4">Search results</h5>
              <div className="row">
                {weather}
              </div>
              </div>
              :
              null
          }
        </main>
      </React.Fragment>
    );
  }
}

export default withRouter(Dashboard)