import React, { Component } from 'react'
import { alert } from '../components/Alert';
import { withRouter } from 'react-router-dom';

class Login extends Component {

    state = {
        username: '',
        password: ''
    }

    componentDidMount() {
        sessionStorage.removeItem('typedUsername');
        sessionStorage.removeItem('typedPassword');
    }

    handleChange = (state, value) => {
        this.setState({
            [state]: value
        });
    }

    login = (event) => {
        const username = sessionStorage.getItem('user');
        const password = sessionStorage.getItem('pass');

        if (this.state.username === username && this.state.password === password) {
            sessionStorage.setItem('typedUsername', this.state.username);
            sessionStorage.setItem('typedPassword', this.state.password);
            this.props.history.push('/');
            alert("Successfully logged in", 'success');
        } else {
            alert('Please try again', 'error');
        }
        event.preventDefault();
    }

    render() {
        return (
            <form className="form-signin">
                <h1 className="h3 mb-3 font-weight-normal">Please sign in</h1>
                <label htmlFor="inputUsername" className="sr-only">Username</label>
                <input type="email" id="inputUsername" className="form-control" placeholder="Username" required="" onChange={(e) => this.handleChange('username', e.target.value)} />
                <label htmlFor="inputPassword" className="sr-only">Password</label>
                <input type="password" id="inputPassword" className="form-control" placeholder="Password" required="" onChange={(e) => this.handleChange('password', e.target.value)} />
                <button className="btn btn-lg btn-primary btn-block" type="submit" onClick={this.login}>Sign in</button>
            </form>
        )
    }
}

export default withRouter(Login)